.Hero {
  position: relative;
  height: 100vh;
  overflow: hidden;
  padding-top: 70px;

  .PhotoContainer {
    img {
      // image-rendering: pixelated;
      object-fit: contain;
      object-position: bottom;
      height: 90vmin;
      width: auto;
      z-index: 3;
    }
  }

