.BackgroundCircle {
  background-color: #e9456046;
  width: 80vmin;
  max-width: 90%;
  height: 80vmin;
  border-radius: 50%;
  position: absolute;
  bottom: 0;
}

.Particles {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 0;
  margin: 0;
  z-index: -1;
}